export function getCurrentDateChinese(){
    var d = new Date(),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return year+"年"+ month+"月"+day+"日";
}

export function formateDateTo8(date){
    var d = new Date(date),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return year+""+month+""+day;
}

export function formateDate8(date, placement) {
    if (date == undefined || date.length < 8) {
        return date;
    };
    let year = date.substring(0, 4);
    let month = date.substring(4, 6);
    let day = date.substring(6, 8);
    if (placement == undefined || placement == '') {
        return year + "年" + month + "月" + day + "日";
    };
    let ret = year + "" + placement + month + placement + day;
    return ret;
}

export function getFormatDate10to8(date10) {
    if (!date10) {
        return
    }
    if (date10 == undefined || date10.length < 10) {
        return date10;
    } else {
        let year = date10.substring(0, 4);
        let month = date10.substring(5, 7);
        let day = date10.substring(8, 10);
        return year + "" + month + "" + day
    }
}


export function NumberToChinese(num) {
    var unitPos = 0;
    var strIns = '',
        chnStr = '';
    var needZero = false;

    if (num === 0) {
        return chnNumChar[0];
    }

    while (num > 0) {
        var section = num % 10000;
        if (needZero) {
            chnStr = chnNumChar[0] + chnStr;
        }
        strIns = SectionToChinese(section);
        strIns += (section !== 0) ? chnUnitSection[unitPos] : chnUnitSection[0];
        chnStr = strIns + chnStr;
        needZero = (section < 1000) && (section > 0);
        num = Math.floor(num / 10000);
        unitPos++;
    }

    return chnStr;
}

