var countdown = 10;
export function countDownTime(context) {
    countdown = 10;
    settime(context)
}



function settime(context) {

    if (countdown == 0) {
        //结束计时，做业务
        context.timeStr = countdown + "s";
        if (typeof(context.countDownCallBack) == "function") {
            context.countDownCallBack();
        };

        return;
    } else {
        context.timeStr = countdown + "s";
        //console.log("time"+countdown)
        countdown--;
    }
    setTimeout(function() {
        settime(context)
    }, 1000)
}



