export function getTime6(time6) {
    if (!time6) {
        return ''
    }
    let year = time6.substring(0, 2);
    let month = time6.substring(2, 4);
    let day = time6.substring(4, 6);
    var str = ''
    if (year != "00") {
        str = str + (parseInt(year) + 0) + "年"
    }
    if (month != "00") {
        str = str + (parseInt(month) + 0) + "个月"
    };
    if (day != "00") {
        str = str + (parseInt(day) + 0) + "天"
    };
    return str;
}

