export function getTime9(sourcetime9) {
    if (!sourcetime9) {
        return
    }
    if (sourcetime9 == undefined) {
        return ''
    }
    var str = '';
    if (typeof(sourcetime9) == "undefined") {
        str = '';
    } else if (sourcetime9.length > 7) {
        var year = sourcetime9.substr(0, 4);
        var month = sourcetime9.substr(4, 2);
        var day = sourcetime9.substr(6, 2);

        str = year + '-' + month + '-' + day;
    } else {
        str = sourcetime9;
    }
    return str;
}
//201708091423 返回 2017-08-09 14:23
export function getTimeof12(sourcetime12) {
    if (!sourcetime12) {
        return
    }
    var str = '';
    if (typeof(sourcetime12) == "undefined") {
        str = '';
    } else if (sourcetime12.length > 13) {
        var year = sourcetime12.substr(0, 4);
        var month = sourcetime12.substr(4, 2);
        var day = sourcetime12.substr(6, 2);
        var hour = sourcetime12.substr(8, 2);
        var min = sourcetime12.substr(10, 2);
        var sec = sourcetime12.substr(12, 2);

        str = year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + sec;
    } else {
        str = sourcetime12;
    }
    return str;
}


