export function addTouchListener(context,touchContainer){
    var nStartX = 0;
    var nStartY = 0;
    var nChangX = 0;
    var nChangY = 0;
    touchContainer.on('touchstart',function(e){
      nStartY = e.targetTouches[0].pageY;
      nStartX = e.targetTouches[0].pageX;
      console.log("touchStart:"+nStartX+"startY:"+nStartY);
    });
    touchContainer.on('touchend',function(e){
      nChangY = e.changedTouches[0].pageY;
      nChangX = e.changedTouches[0].pageX;
      console.log("touchEnd: nStartX"+nStartX+"startY:"+nStartY+"endx:"+nChangX+"endY:"+nChangY);
      if ((nStartY - nChangY)>6) {
        console.log("cancel anim");
        context.showGif = false;
      };
    });
}

export function getUrlKey(name) {
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null
}



