//获取审批状态中文
export function getApprStatusCn(stat){
    if (stat == 'A01' || stat == '001') {
        return '审批中'
    }else if (stat == 'A02' || stat == '002') {
        return '审批失败'
    }else if (stat == 'A03' || stat == '003') {
        return '审批通过'
    }else if (stat == 'A04' || stat == '004') {
        return '审批作废'
    }else if (stat == 'H00') {
        return '未审核放款'
    }else if (stat == 'H01') {
        return '同意放款，但贷款支用申请还未发送到浦发总行'
    }else if (stat == 'H02') {
        return '禁止放款'
    }else{
        return stat
    }
}
 export function getRepaytype(code) {
            // 1 - 等额本息
            // 3 - 一次还本付息
            // 4 - 分次付息一次还本
            var repaytype = '--';
            if (code == undefined) {
                return repaytype
            }
            if (code == 1) {
                repaytype = '等额本息';
            }else if (code == 2) {
                repaytype = '等额本金';
            } else if (code == 3) {
                repaytype = '到期一次性归还本息、利随本清';
            } else if (code == 4) {
                repaytype = '分期付息，到期一次还本';
            } else if (code == 'y') {
                repaytype = '网贷组合还款方式1';
            } else if (code == 'z') {
                repaytype = '网贷组合还款方式2';
            }
            return repaytype;

 }


